	width: 60px;
	margin: 1rem;
	margin-left: 0;
	display: block;
}

.client-code {}

.job-number {
	color: $white;
}

.project-name {
	color: $dsblue;
}

.texture-code-reference {

	margin-bottom: 1rem;
	font-size: 1.1rem;

	li {
		margin-bottom: 0.5rem;
		list-style-type: none;
	}

	display:flex;
	flex-direction: row;

	&__code {
		flex-basis: 100px;
		color: $white;

		li {
			&::before {
				content: "_";
				color: $midgrey;
			}
		}
	}

	&__name {
		&__indicated {
			li::before {
				content: "◼";
				color: var(--bullet-color);
				display: inline-block;
				width: 1.5em;
				margin-left: -1em;
			}
		}
	}
}

.unreal-code-reference {
	display: grid;
	grid-template-columns: 1fr 1fr;
	margin-top: 2rem;

	h1 {
		font-size: 0.9rem;
		letter-spacing: 0.05rem;
		font-weight: 800;
	}

	.line-item {
		display: grid;
		grid-template-columns: 4rem 6rem 1fr;
		gap: 1rem;
		margin-bottom: 1rem;
		align-items: center;


	}

	.line-items--non-prefixed {
		.line-item {
			grid-template-columns: 4rem 10rem 1fr;

			.code {
				&::after {
					content: "";
				}
			}
		}
	}

	.code {
		color: $white;

		&::after {
			content: "_";
			color: $midgrey;
		}
	}

	.example {
		color: white;
	}

	.name {
		// &__indicated {
		// 	&::before {
		// 		content: "◼";
		// 		color: var(--bullet-color);
		// 		display: inline-block;
		// 		width: 1.5em;
		// 		margin-left: -1em;
		// 	}
		// }
	}
}

$wire: #444444;

.proj-source-layout {
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 2rem;

	>* {
		padding: 2rem;
		background: $darkergrey;
	}

}

.no-render {
	display: none;
}

.software-selection {
	display: flex;
	flex-direction: row;
	gap: 1rem;
	margin-top: 2rem;
	margin-bottom: 2rem;

	button {
		border: 0;
		background-color: $darkergrey;
		padding: 1rem 2rem;
		color: #888;
		//border-radius: 0.25rem;
		display: flex;
		flex-direction: row;
		align-items: center;
		gap: 1rem;
		transition-duration: 200ms;
		border-bottom: 3px solid rgba(0, 0, 0, 0);

		$iconSize: 4rem;
		font-size: 1.5rem;


		svg {
			width: $iconSize;
			height: $iconSize;
			fill: #ddd;
			transition-duration: 200ms;
			transition-timing-function: ease-out;
		}

	}

	.active {
		border-bottom: 3px solid $dsblue;
		color: white;

		svg {
			fill: white;
			transform: scale(1.1);
		}
	}
}

.folder-view {


	ul {
		margin-left: 10px;
		padding-left: 20px;
		//border-left: 1px dashed #ddd;
	}

	li {
		list-style: none;
		color: #888;
		font-size: 17px;
		font-style: italic;
		font-weight: normal;
		margin-bottom: 0.25rem;
		position: relative;

		&:before {
			position: absolute;
			left: -15px;
			top: 0px;
			content: '';
			display: block;
			border-left: 1px solid $wire;
			height: 1em;
			border-bottom: 1px solid $wire;
			width: 10px;
		}

		&:after {
			position: absolute;
			left: -15px;
			bottom: -7px;
			content: '';
			display: block;
			border-left: 1px solid $wire;
			height: 100%;
		}

		&:last-child {
			&:after {
				display: none
			}
		}

		&.root {
			margin: 0px 0px 0px -20px;

			&:before {
				display: none;
			}

			&:after {
				display: none;
			}
		}

		.file {
			.title {
				color: #aaa;
			}
		}

		.icon {
			margin-right: 10px;
			content: "";
			height: 20px;
			vertical-align: middle;
			width: 20px;
			background-repeat: no-repeat;
			display: inline-block;

			&__file {
				background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 482 482' fill='cadetblue'><path d='M315.459,0H121.812C93.521,0,70.496,23.041,70.496,51.315v189.754h-5.012c-11.418,0-20.677,9.251-20.677,20.679v125.404c0,11.427,9.259,20.677,20.677,20.677h5.012v22.995c0,28.305,23.025,51.315,51.316,51.315h264.221c28.275,0,51.3-23.011,51.3-51.315V121.449L315.459,0z M94.507,283.228H120.7l8.518,29.029c2.711,10.057,5.423,20.9,7.361,31.097h0.386c2.452-10.071,5.415-21.546,8.258-31.222l9.291-28.904h25.673l4.775,86.966h-19.227l-1.291-33.286c-0.387-10.466-0.773-23.105-0.773-35.745h-0.385c-2.711,11.095-6.32,23.483-9.677,33.68l-10.584,33.933h-15.351l-9.291-33.68c-2.844-10.197-5.806-22.584-7.871-33.933h-0.261c-0.512,11.743-0.906,25.153-1.551,35.997l-1.546,33.034H88.959L94.507,283.228z M386.033,450.713H121.812c-10.955,0-19.874-8.92-19.874-19.889v-22.995h246.31c11.418,0,20.678-9.25,20.678-20.677V261.748c0-11.428-9.26-20.679-20.678-20.679h-246.31V51.315c0-10.938,8.919-19.858,19.874-19.858l181.89-0.19v67.233c0,19.638,15.934,35.587,35.585,35.587l65.864-0.189l0.741,296.925C405.891,441.793,396.987,450.713,386.033,450.713z M200.125,370.194v-85.815c6.06-1.024,14.578-1.796,26.58-1.796c12.126,0,20.772,2.315,26.579,6.965c5.548,4.382,9.291,11.615,9.291,20.127c0,8.51-2.837,15.745-7.998,20.646c-6.714,6.32-16.642,9.157-28.259,9.157c-2.584,0-4.901-0.128-6.714-0.379v31.096H200.125z M293.457,332.904h-7.998v-14.072h7.612c6.579-0.128,14.964-2.569,14.964-9.677c0-5.029-4.128-8.764-12.388-8.764c-6.839,0-14.058,2.963-17.549,5.027l-3.995-14.2c5.027-3.215,15.098-6.32,25.933-6.32c17.935,0,27.873,9.426,27.873,20.898c0,8.904-5.029,15.87-15.351,19.496v0.252c10.062,1.813,18.186,9.426,18.186,20.393c0,14.832-13.033,25.676-34.316,25.676c-10.844,0-20.009-2.838-24.91-5.942l4.003-14.704c3.483,1.813,11.482,5.169,19.479,5.169c10.197,0,15.359-4.917,15.359-11.237C310.361,336.641,302.101,332.904,293.457,332.904z'/></svg>");
				background-position: center 2px;
				background-size: 60% auto;
			}

			&__folder {
				background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 512 512' fill='slategray' ><path d='M512,160v-32c0-17.674-14.328-32-32-32H250.512c-16.977,0-33.254-6.742-45.258-18.746l-26.508-26.509C166.742,38.742,150.465,32,133.488,32H32C14.328,32,0,46.326,0,64v96H512z'/><path d='M0,192v256c0,17.674,14.328,32,32,32h448c17.672,0,32-14.326,32-32V192H0z'/></svg>");
				background-position: center top;
				background-size: 65% auto;
			}
		}


	}
}

.tab-group {

	.tab-group-content {
		background: $darkergrey;
		padding: 3rem;

		.hidden {
			display: none;
		}
	}

	.tab-group-tabs {
		display: flex;
		gap: 1rem;

		//margin-bottom:2rem;
		div {
			float: left;
			display: block;
			color: $midgrey;
			background-color: $darkergrey;
			padding: 1rem;
			font-weight: 800;

			&.active {
				color: $white;
				background-color: $darkergrey;

			}
		}
	}
}

.wide-container {
	padding-left: 5rem;
	padding-right: 5rem;
}

.rule-sheet {
	font-size: 1.2rem;
	//min-height: 100vh;
	width: 100%;
	//border: 1px solid red;
	align-items: center;
	display: flex;
	padding: 6rem;

	>* {
		flex-grow: 1;
	}

	h1 {
		color: $white;
		font-size: 5rem;
		margin-top: 3rem;
		margin-bottom: 1.5rem;

		&:first-of-type {
			margin-top: 0;
		}
	}

	h2 {
		font-size: 1.5rem;
		margin-bottom: 0rem;
	}

	.quote {
		align-self: flex-end;
		justify-self: right;
		padding: 2rem;
		padding-left: 6rem;
		font-size: 1.5rem;
		font-weight: 400;
		flex-basis: 25%;
		color: $midgrey;
		border: 1rem solid $darkgrey;
		position: relative;

		&:before {
			content: '’’';
			position: absolute;
			left: 0em;
			top: -0.25em;
			padding: 0;
			margin: 0;
			pointer-events: none;
			font-size: 12em;
			text-align: left;
			font-family: Arial;
			letter-spacing: -1.0rem;
			font-style: normal;
			font-weight: 400;
			color: $darkgrey;
		}
	}

}

.variant-table {

	margin-top:2rem;
	$iconSize: 1.5rem;

	.line-item {
		display: grid;
		grid-template-columns: $iconSize 25% 30% 1fr;
		gap: 1rem;

		&>*:nth-child(4) {
			text-align: right;
			color: white;
		}

		h1 {
			font-size: 1.2rem;
			letter-spacing: 0.05rem;
			color: white !important;
		}
	}
}

.general-rules {
	font-size: 1.25rem;

	li {
		margin-bottom: 0.5rem;
		list-style-type: square;
		margin-left: 2rem;
	}
}


.principal {

	padding: 1.5rem;
	background: $darkergrey;
	margin-bottom: 1rem;
	background-color: rgba($color: #000000, $alpha: 0.2);
	border-radius: 0.2rem;
	box-shadow: 0 0 5px 5px rgba($color: #000000, $alpha: 0.2);

	.title {
		display: block;
		color: $white;
		font-size: 4rem;
		margin-bottom: 0.5rem;
	}

	.subtitle {}
}

.project-subfolders {
	h1 {
		letter-spacing: 0.1rem;
		text-transform: none;
		;
	}
}

.rgb-demo {
	margin-top: 1rem;

	img {
		padding: 3px;
	}

	&-channels {
		img {
			width: 80%;
		}
	}
}

.project-examples {
	padding: 1rem;

	.example {
		margin-bottom: 6rem;
	}
}

.project-checklist {
	display: grid;
	grid-template-columns: 1fr 1fr;
	list-style-type: none;
	margin-bottom: 2rem;
	$iconSize: 1.25rem;


	.icon {
		height: $iconSize;
		width: $iconSize;
	}

	li {
		margin-bottom: 0.5rem;
		display: flex;
		flex-direction: row;
		gap: 0.5rem;
		align-items: center;
	}
}

.project-assets {

	background-color: #242424;
	border: 1px solid rgba(0, 0, 0, 0.5);
	box-shadow: 0.05rem 0.05rem 1rem rgba(0, 0, 0, 0.75);

	&--path {
		display: flex;
		flex-direction: row;
		gap: 0.5rem;
		align-items: center;
		$icon-size: 1.5rem;
		padding: 0.5rem;
		border-bottom: 1px solid #1a1a1a;

		svg {
			fill: #aaa;
			width: $icon-size;
			height: $icon-size;
		}

	}

	&--assets {
		display: flex;
		flex-direction: row;
		gap: 0.75rem;
		padding: 0.5rem;
	}
}

.unreal-asset {
	border: 1px solid rgba(0, 0, 0, 0.5);
	display: flex;
	flex-direction: column;
	width: 150px;
	box-shadow: 0.05rem 0.05rem 1rem rgba(0, 0, 0, 0.75);
	position: relative;

	.graphic {
		border-bottom: 3px solid;
	}

	.label-area {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		gap: 1.5rem;
		padding: 0.5rem;
	}

	h1,
	h2 {
		text-transform: none;
		letter-spacing: 0;
		font-weight: 400;
		word-wrap: break-word;
		margin: 0;
	}

	h1 {
		font-size: 0.9rem;
	}

	h2 {
		font-size: 0.75rem;
	}

	$scoreSize: 1.75rem;
	$scorePadding: 0.25rem;

	.score {
		position: absolute;
		top: $scorePadding;
		right: $scorePadding;
		width: $scoreSize;
		height: $scoreSize;


		svg {
			width: $scoreSize;
			height: $scoreSize;
		}

		z-index: 2;
	}

}