.services-featured {
	text-align: center;
	font-size: 1.7rem;
	line-height: 1.6;
	font-weight: 400;
	padding-left: 4rem;
	padding-right: 4rem;
	color: $midgrey;

	@include breakpoint-up(medium) {
		font-size: 3rem;
		padding-left: 0;
		padding-right: 0;
	}

	.container {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-items: center;
		column-gap: 4rem;

		@include breakpoint-up(large) {
			flex-direction: row;
		}
	}

	.left {
		@include breakpoint-up(large) {
			text-align: left;
			flex-basis: 50%;
		}
	}

	.right {
		@include breakpoint-up(large) {
			text-align: right;
			flex-basis: 50%;
		}
	}

	.intro {
		strong {
			color: #fff;
		}

		.primary {
			font-weight: 300;
			display: block;
			color: $lightgrey;
			margin-bottom: 1rem;

			.title {
				font-weight: 700;
				color: $white;
			}
		}

		.secondary {
			font-size: 1.15rem;
			line-height: 1.75;
			letter-spacing: 0.3rem;

			@include breakpoint-up(medium) {
				font-size: 1.4rem;
			}
		}
	}

	.logo {
		height: 4rem;
		margin-top: 4rem;
		margin-bottom: 3rem;		

		@include breakpoint-up(large) {
			margin-top: 0;
			margin-bottom: 6rem;
		}

		svg {
			height: 100%;
			fill: $white;
		}
	}

	nav {
		text-transform: uppercase;
		font-weight: 300;
		font-size: 0.9rem;
		color: $midgrey;
		letter-spacing: 0.5rem;
		line-height: 2;

		@include breakpoint-up(medium) {
			font-size: 1.3rem;
		}

		.service,
		.service:active,
		.service:visited {
			color: $lightgrey;
			display: block;
			margin-bottom: 2rem;
			cursor: pointer;

			&:hover {
				color: $white;
			}
		}

		&::after {
			content: "";
			display: block;
			background-color: $dsblue;
			height: 6px;
			width: 20%;
			margin: 2rem auto;

			@include breakpoint-up(large) {
				float: right;
			}
		}
	}
}