.services-nav {
	display: flex;
	flex-direction: column;
	gap: 4rem;

	align-items: center;
	justify-content: center;
	min-height: 100vh;

	font-size: 1.5rem;
	text-transform: uppercase;
	letter-spacing: 0.75rem;
	font-weight: 300;
	color: $midgrey;
}

.additional-images {
	display: flex;
	flex-direction: row;
	max-width: 100%;
	flex-wrap: wrap;
	align-content: center;
	justify-content: center;
	gap: 1rem;
	margin-top: 1rem;
	margin-bottom: 1rem;

	img {
		flex: 0 1 10%;
		width: 20%;
		max-width: 50%;
		max-height: 300px;
	}
}

.service-entry {
	display: flex;
	flex-direction: column;

	@include breakpoint-up(large) {
		flex-direction: row;
	}

	&__full {
		min-height: 100vh;
		align-items: stretch;
	}

	.additional-content {
		margin-top: 2rem;
	}

	&--img {
		@include breakpoint-up(large) {
			position: relative;
		}

		overflow:hidden;
		display:inline-block;

		img {
			min-width: 100%;
			min-height: 100%;
			width: 100%;
			height: 100%;

			@include breakpoint-up(large) {
				position: absolute;
			}

			object-fit: cover;
			transition-duration: 3s;
		}

		img.focused {
			transform: scale(1.035);
		}

		@include breakpoint-up(large) {
			flex-basis: 60%;
		}
	}



	&--body {
		padding: 2rem;
		background-color: $darkergrey;
		opacity: 1;
		transition-duration: 1s;

		@include breakpoint-up(large) {
			flex-basis: 40%;
			padding: 4rem;
		}

		&.focused {
			opacity: 1;
		}

		&__full {
			align-self: center;
		}

		p:last-of-type {
			margin-bottom: 0;
			font-weight: 400;
		}
	}

	h1 {
		font-size: 0.9rem;
		text-transform: uppercase;
		font-weight: 300;
		letter-spacing: 1.2rem;

		@include breakpoint-up(large) {
			font-size: 2rem;
		}
	}

	p {
		font-size: 1rem;
		font-weight: 300;
		letter-spacing: 0.05rem;
		line-height: 2;

		@include breakpoint-up(large) {
			font-size: 1.25rem;
		}
	}

	.button {
		text-transform: uppercase;
		font-weight: 700;
		transform: translateX(-1rem);
		margin-top: 1.5rem;
		float: left;
		padding: 1rem;
		color: $lightgrey;
		display: flex;
		align-items: center;
		transition-timing-function: ease-in-out;
		transition-duration: 0.15s;
		cursor: pointer;

		@include breakpoint-up(large) {
			font-size: 1.2rem;
			padding-left: 2rem;
			transform: translateX(-2rem);
		}

		svg {
			fill: $white;
			max-height: 3rem;
			max-width: 3rem;
			padding-left: 1rem;
		}

		&:hover {
			color: $white;

			&>span {
				margin-left: 0.75rem;
				color: $dsblue;
			}
		}

		>span {
			margin-left: 0.5rem;
			transform: scale(1.5);
			transition-timing-function: ease-in-out;
			transition-duration: 0.15s;
		}
	}
}

section:nth-of-type(even)>.service-entry>.service-entry--body {
	@include breakpoint-up(large) {
		order: -1;
	}
}